body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.about {
  margin-top: 35px;
}

.image-container {
  text-align: center;
}

.picture {
  max-width: 75%;
  margin-bottom: 15px;
}

.title {
  font-weight: bold;
}

.text{
  font-size: 18px;
  margin-bottom: 10px;
}

.bar-wrapper {
  margin-bottom: 100px;
}

.nav-title{
  width: 180px;
  height: 30px;
}

.bar-wrapper > nav {
  box-shadow: 2px 2px 2px rgb(211, 211, 211);
}

.navbar-toggler {
  border-color: transparent !important;
  outline: 0px !important;
}

.element-wrapper{
  margin-right: 30px;
}

.bar-elements {
  padding: 10px;
}

.bar-elements:hover{
  cursor: pointer;
  background-color: rgba(211, 211, 211, 0.2);
}


.app{
  font-family: 'Work Sans', sans-serif;
}

.content {
  max-width: 1000px;
  margin: auto;
  padding-bottom: 50px; 
  padding-left: 15px;
  padding-right: 15px;
}

.content > div {
  margin-top: 30px;
  margin-bottom: 30px;
  width: 100%;
}

.box-wrapper {
  border-style: solid;
  border-width: 2px;
  border-color: lightgray;
  border-radius: 10px;
  box-shadow: 4px 4px 4px rgba(211, 211, 211, 1);
  padding: 30px;
  margin-top: 40px;
}

.icon-wrapper{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}


.icon {
  width: 100px;
  height: 100px;
}

.name {
  font-style: italic;
  font-weight: bold;
  margin-top: 20px;
  margin-bottom: 0px;
  text-align: center;
}

.skill-container > h4{
  font-style: italic;
  margin-top: 50px;
}

.skill-icon{
  padding-right: 10px;
}

.tool-icon {
  width: 40px;
  height: 40px;
  margin-top: 10px;
  margin-right: 10px;
}

.proIcon-container {
  text-align: center;
}

.project-icon {
  max-width: 60%;
  margin-bottom: 15px;
}

.tool-container {
  padding-left: 15px;
  padding-right: 15px;
}

.button-icon {
  width: 35px;
  height: 35px;
  margin-right: 10px; 
}

.contract-button {
  width: 130px;
  height: 50px;
  border-radius: 10px; 
  box-shadow: 2px 2px 2px rgba(211, 211, 211, 1);
  background: transparent;
}

